import Header from "@/components/Header";
import Footer from "@/components/Footer";
import Workflow from "@/components/workflows/Workflow";

// import { useParams } from "next/navigation";
import { Suspense } from "react";

export default async function WorkflowPage({params}) {
    // const params = useParams();
    const { id } = await params;
    const url = process.env.SERVER_URL || ""
    console.log(url + "/workflows?workflowId=" + id);
    const data = await fetch(url + "/workflows?workflowId=" + id,
        {
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            }
        }
    )
    const workflow = (await data.json())[0];
    console.log(workflow);

    return (
        <div>
            <Header />
            <main>
                <div>
                    <h2>Metadata</h2>
                    <div>
                        {(workflow.base_action.metadata).map((data) => {
                            <div key={data.id}>
                                data
                                {/* {JSON.stringify(data)} */}
                            </div>
                        })}
                    </div>
                </div>
                {JSON.stringify(workflow.base_action.metadata)}
                {/* <Suspense fallback={<div>Loading...</div>}>
                    <Workflow /> TODO: Wrap this in a suspense, replace with a skeleton while loading
                </Suspense> */}
            </main>
            <Footer />
        </div>
    )
}